* {
  font-family: Arial, Helvetica, sans-serif;
}


body {
  padding: 60px;
}

input {
  color: gray;
  width: 100%;
  font-size: 1.2rem;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin: 10px 10px;
  overflow: hidden;
  width: 170px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
  min-width: 170px;
}

.circular-indicator{
  display: flex;
  justify-content: center;
  text-align: center;
}
